.masthead {
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  margin-bottom: 1rem;
}

.head {
  position: relative;
  margin-top: 1.5rem;

  .navigation  {
    position: absolute;
    top: 0; left: 0;
    width: 100%;

    .left, .right {
      display: inline-block;
      list-style: none;
      font-size: 17px;
      margin: 1rem 0 0 0;
      padding: 0;
    }

    .left {
      float: left;

      li {
        margin: 0 1rem;
      }
    }

    .right {
      float: right;

      &:after {
        content: '';
        display: block;
        clear: both;
      }

      li {
        margin: 0 1rem;
      }
    }

    li {
      display: inline-block;

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }

      a {
        display: block;
        line-height: 24px;
        box-shadow: inset 0 -2px rgba(212,203,195,0);
        -webkit-transition: .1s linear box-shadow;
        -moz-transition: .1s linear box-shadow;
        transition: .1s linear box-shadow;

        &:hover, &:focus {
          text-decoration: none;
        }

        &:active {
          position: relative;
          top: 2px;
          line-height: 24px;
        }
      }

      a:hover, &.active a {
        box-shadow: inset 0 -2px rgba(212,203,195,1);
      }
    }

    a {
      color: #000000;
    }
  }

  .masthead-title {
    width: 100%;
    display: block;
    text-align: center;
    margin: 0 0.5rem;
    color: #000000;

    .logo {
      display: block;
      width: 14rem;
      margin: 0 auto;
      overflow: visible;

      path {
        fill: rgba(0,0,0,1);
        -webkit-transition: 0.1s linear fill;
        -moz-transition: 0.1s linear fill;
        transition: 0.1s linear fill;
      }
    }

    a {
      position: relative;
      display: inline-block;
      color: inherit;
      z-index: 10;

      &:hover .logo path {
        fill: rgba(0,0,0,0);
        stroke: rgba(0,0,0,0.5);
        stroke-width: 2px;
      }

      &:active .logo {
        position: relative;
        top: 2px;
      }
    }

    .menu {
      display: none;
      position: relative;
      float: right;
      width: 17px + 6px + 6px;
      height: 17px + 6px + 6px;
      padding: 6px;
      margin-right: -6px;
      cursor: pointer;

      span {
        position: absolute;
        display: block;
        width: 6px;
        height: 6px;
        background: #000;
        border-radius: 3px;
        transform-origin: 3px 3px;
        -webkit-transition: 0.2s ease-in-out width, 0.2s ease-in-out height, 0.2s ease-in-out border-radius, 0.2s ease-in-out transform-origin;
        transition: 0.2s ease-in-out width, 0.2s ease-in-out height, 0.2s ease-in-out border-radius, 0.2s ease-in-out transform-origin;

        &:nth-child(1) {
          top: 6px; left: 6px;
          transform: rotate(-45deg);
        }

        &:nth-child(2) {
          top: 6px; right: 6px;
          transform: rotate(45deg);
        }

        &:nth-child(3) {
          bottom: 6px; left: 6px;
          transform: rotate(45deg);
        }

        &:nth-child(4) {
          bottom: 6px; right: 6px;
          transform: rotate(-45deg);
        }
      }

      &.active span {
        width: 3px;
        height: 22.75px;
        border-radius: 1.5px;
        transform-origin: 1.5px 1.5px;

        &:nth-child(n+3) {
          transform-origin: 1.5px 21.25px;
        }
      }
    }
  }
}

.hair {
  &:after {
    content: '';
    display: block;
    clear: both;
  }

  img {
    margin: 0;
    padding: 0 4px;
    width: auto;
    height: 14px;
  }

  li:first-child img {
    padding-left: 0;
  }

  li:last-child img {
    padding-right: 0;
  }
}

.social {
  float: left;
  list-style: none;
  margin: 0;
  padding: 0;

  li {
    display: inline-block;
    vertical-align: bottom;
    opacity: 0.2;
    -webkit-transition: .1s linear opacity;
    -moz-transition: .1s linear opacity;
    transition: .1s linear opacity;

    &:hover {
      opacity: 1;
    }

    &:active {
      position: relative;
      top: 1px;
    }
  }
}

.search {
  position: relative;
  float: right;
  opacity: 0.2;
  z-index: 100;
  -webkit-transition: .1s linear opacity;
  -moz-transition: .1s linear opacity;
  transition: .1s linear opacity;

  img {
    display: inline-block;
    vertical-align: bottom;
    cursor: pointer;
  }

  #search {
    width: 100px;
    height: 26px;
    background: rgba(255,255,255,0);
    border: 0;
    padding-right: 10px;
    box-shadow: 0 2px rgba(0,0,0,0.5);
    outline: none;
    font-family: var(--body-font);
    font-size: 16px;
    line-height: 16px;
    -webkit-appearance: none;
    -webkit-transition: .1s linear width, .1s linear background;
    -moz-transition: .1s linear width, .1s linear background;
    transition: .1s linear width, .1s linear background;
  }

  &:hover {
    opacity: 1;

    #search {
      box-shadow: 0 2px rgba(0,0,0,1);
    }
  }

  input[type="search"]::-webkit-search-cancel-button {
    display: none;
  }
}

.searching {
	.search {
	  opacity: 1;

	  #search {
	    width: 200px;
	    box-shadow: 0 2px rgba(0,0,0,1);
	  }
	}
}

@media (max-width: 60rem) {
  .head {
    .masthead-title {
      margin: 0;
    }

    .navigation {
      position: static;
      text-align: center;
      margin-top: 0.5rem;
      
      .left, .right {
        float: none;
      }

      .left li:last-child, .right li:first-child {
        margin: 0 1rem;
      }
    }
  }
}

@media (max-width: 35rem) {
  .hair, .navigation {
    display: none;
  }

  .masthead {
    padding-top: var(--spacer-2);
    padding-bottom: var(--spacer-2);
    margin-bottom: var(--spacer-2);
    box-shadow: 0 1px rgba(0,0,0,0.1);

    .head {
      margin-top: 0;

      .masthead-title {
        text-align: left;
        margin: 0;
        line-height: 0.5;

        .logo {
          width: 8rem;
        }

        .menu {
          display: block;
        }
      }
    }
  }
}

@media (prefers-color-scheme: dark) {
  .masthead img, .logo {
    filter: invert(100%);
  }

  .head {
    .navigation {
      a {
        color: #fff;
      }

      li {
        a {
          box-shadow: inset 0 -2px rgba(255,255,255,0);
        }
        a:hover, &.active a {
          box-shadow: inset 0 -2px rgba(255,255,255,0.2);
        }
      }
    }

    .masthead-title {
      color: #fff;

      .menu span {
        background: #fff;
      }
    }
  }

  .search {
    #search {
      color: #fff;
      box-shadow: 0 2px rgba(255,255,255,0.5);
    }

    &:hover #search {
      box-shadow: 0 2px rgba(255,255,255,1);
    }
  }

  .searching .search #search {
    box-shadow: 0 2px rgba(255,255,255,1);
  }

  @media (max-width: 35rem) {
    .masthead {
      box-shadow: 0 1px rgba(255,255,255,0.1);
    }
  }
}
